.Profile{
    padding: 24px;
    align-self: center;
    text-align: center;
    .Name{
        color: var(--color-Black);
    }
    .Role{
        color: var(--color-Gray2);
    }
}
.Friends{
    margin: 16px 0;
    display: flex;
    flex-direction: column;
}
.UserMedia{
    padding: 16px 0;
    .Content{
        display: flex;
        flex-flow: wrap;
        justify-content: space-between;
        
    }
    .ButtonsWrapper{
        margin-top: 16px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
}