.PhotoMedia{
    background: #C4C4C4;
    box-shadow: 0px 2.57644px 2.57644px rgba(50, 50, 71, 0.08), 0px 2.57644px 5.15289px rgba(50, 50, 71, 0.06);
    border-radius: 6.44111px;
    height: 100px;
    width: 100px;
    margin-top: 16px;
    position: relative;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    // background-image: url("./photo/Rectangle\ 6.png");
}
.AvatarMain{
    border-radius: 50%;
    width: 118px;
    height: 118px;
    position: relative;
    background-size: 100%;
    margin-bottom: 24px;
}
.AvatarSecondary{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 16px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
}